.Container {
    min-height: 20rem;
}

.BtnPostCancel {
    border-radius: 15px 0 15px 15px;
    color: #002155;
    background-color: #e6eefb;
    border: none;
    padding: 0.25rem 0;
    width: 40%;
    margin-right: 1rem;
}

.BtnPostCreate {
    border-radius: 0 15px 15px 15px;
    color: #e6eefb;
    background-color: #00317f;
    padding: 0.25rem 0;
    border: none;
    width: 40%;
}

.FileInput {
    display: none;
}

.ChangeImage {
    cursor: pointer;
}

.PostContainer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(28rem, 1fr));
    gap: 1rem;
    row-gap: 1rem;
}

figure {
    width: 100%;
    height: 40vh;
    object-fit: cover;
}